* {
    margin: 0;
    padding: 0;
}

html {
    font-size: 62.5%;
}
.loginWrapper {
    width: 100%;
    height: 100%;
}

.loginWrapper h2 {
    font-size: 5rem;
    color: white;
    cursor: pointer;
}

.registerWrapper h2 {
    font-size: 5rem;
    color: white;
    cursor: pointer;
}

.formsContainer {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 40%;
    height: 60%;
    transform: translate(-50%, -50%);
    padding: 10rem 5rem;
    display: flex;
    flex-direction: column;
    background-color: lightblue;
    border-radius: 10px;
}

.loginForm {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 5rem 5rem;
}

.loginForm .loginField {
    padding: 1rem 2rem;
    width: 100%;
    margin: 1rem;
    border: none;
    box-shadow: 0 .5rem .5rem rgba(0, 0, 0, .1);
    border-radius: 5px;
    font-size: 1.5rem;
    position: relative;
}

.loginForm .loginField:focus {
    border: none;
    border-bottom: 2px solid #566068;
}

.loginForm .loginField:focus+label {
    top: -1rem;
    font-size: 1rem;
}

.loginForm .loginField:valid+label {
    top: -1rem;
    font-size: 1rem;
}

.loginEmail {
    position: relative;
    margin: .5rem;
    width: 100%;
}

.loginPassword {
    position: relative;
    margin: .5rem;
    width: 100%;
}

.loginForm .loginSubmit {
    border: none;
    background-color: white;
    color: black;
    padding: 1rem 2rem;
    font-size: 2rem;
    margin-top: 2rem;
    border-radius: 10px;
    transition: all .2s;
    text-decoration: none;
}

.loginForm .loginSubmit:hover {
    box-shadow: 0 .5rem .5rem rgba(0, 0, 0, .1);
    transform: translateY(-1px);
}

.loginForm label {
    font-size: 2rem;
    position: absolute;
    margin: 0;
    margin-left: 4rem;
    width: 100%;
    left: -1.5rem;
    top: 1.5rem;
    pointer-events: none;
    color: #566068;
    transition: 0.3s;
}


