}

.banner img {
    width: 100%;
}

.medallion {
    position: relative;
    margin: 3rem;
    border-radius: 1rem;
    background-color: transparent;
    perspective: 1000px;
    width: 40%;
    height: 22rem;
}

.medallion_inner {
    position: relative;
    border-radius: 1rem;
    box-shadow: 0 1rem 1rem rgba(0, 0, 0, .15);
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
}

.medallion:hover .medallion_inner {
    transform: rotateY(180deg);
}

.front,
.back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.front {
    background-color: white;
}

.back {
    background-color: white;
    color: white;
    transform: rotateY(180deg);
}

.flexWrapper {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.medallion__pic {
    width: 18rem;
    height: 18rem;
    padding: 1.5rem;
}

.clock__pic {}

.name__wrapper {
    border-radius: 50%;
    background-color: lightblue;
    width: 15rem;
    height: 15rem;
    text-align: center;
    /* position: relative;
    margin: 0rem 2rem; */
    background-image: url(/static/media/pfp_1.9fe01bc6.png);
    background-size: cover;
}

.medallion__name {
    font-size: 1.75rem;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
}

.medallionContent {
    font-size: 1.75rem;
    text-align: center;
}

.pendingError {
    margin-top: 5rem;
    font-size: 4rem;
    box-shadow: 0 1rem 1rem rgba(0, 0, 0, .1);
    border-radius: 15px;
    padding: 1rem;
    color: gray;
    width: 45rem;
    text-align: center;
}

.complete {
    font-size: 2.5rem;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: green;
    border: none;
    box-shadow: 0 .5rem .5rem rgba(0, 0, 0, .1);
    padding: 1rem;
    border-radius: 1.5rem;
}

.accept {
    font-size: 2.5rem;
    position: absolute;
    top: 50%;
    left: 40%;
    transform: translate(-50%, -50%);
    background-color: green;
    border: none;
    box-shadow: 0 .5rem .5rem rgba(0, 0, 0, .1);
    padding: 1rem;
    border-radius: 1.5rem;
}

.reject {
    font-size: 2.5rem;
    position: absolute;
    top: 50%;
    left: 60%;
    transform: translate(-50%, -50%);
    background-color: rgb(204, 53, 53);
    border: none;
    box-shadow: 0 .5rem .5rem rgba(0, 0, 0, .1);
    padding: 1rem;
    border-radius: 1.5rem;
}

.complete:hover,
.accept:hover,
.reject:hover {
    box-shadow: 0 .5rem .5rem rgba(0, 0, 0, .2);
    transform: translate(-50%, -51%);
}

.complete:active,
.accept:active,
.reject:active {
    box-shadow: 0 .25rem .25rem rgba(0, 0, 0, .05);
    transform: translate(-50%, -49%);
}
