.friendsWrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    padding-left: 5rem;
    padding-right: 5rem;
    min-height: 20%;
}

.friendRequestsWrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 1rem;
    padding-left: 3rem;
    padding-right: 3rem;
    min-height: 20%;
}

.friendRequests {
    display: flex;
}

[data-status='true'] {
    visibility: visible!important;
    opacity: 1!important;
}

.friendModalCover {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .3);
    z-index: 100;
    transition: all .3s;
}

.friendModal {
    width: 30%;
    max-height: 60%;
    background-color: white;
    position: absolute;
    z-index: 101;
    left: 50%;
    top: 50%;
    border-radius: 20px;
    box-shadow: 0 1rem 1rem rgba(0, 0, 0, .2);
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 2rem;
}

.userSearch {
    width: 20rem;
    font-size: 2rem;
    border: none;
    border-radius: 15px;
    padding: 0rem 1rem;
    background-color: rgba(0, 0, 0, .1);
    text-align: center;
}

.userSearch:focus {
    outline: none;
    background-color: rgba(0, 0, 0, .075)
}

.userResults {
    width: 80%;
    padding-top: 1rem;
    padding-bottom: 1rem;
}

.userResults button {
    border: none;
    background-color: rgba(0, 0, 0, .1);
    box-shadow: 0 .2rem .2rem rgba(0, 0, 0, .05);
    border-radius: 10px;
    width: 100%;
    font-size: 1.6rem;
    text-align: center;
    cursor: pointer;
    margin: 1rem 0;
}

.userResults button:hover {
    box-shadow: 0 .2rem .2rem rgba(0, 0, 0, .1);
    transform: translate(-1px, -1px);
}

.userResults button:active {
    box-shadow: 0 .1rem .1rem rgba(0, 0, 0, .02);
    transform: translate(1px, 0px);
}

.friendRequests div {
    border-radius: 50%;
    background-color: lightblue;
    width: 15rem;
    height: 15rem;
    text-align: center;
    position: relative;
    margin: 0rem 2rem;
}

.friendRequests div .friendName {
    font-size: 1.75rem;
    position: absolute;
    width: 15rem;
    top: 15rem;
    left: 0rem;
}

.friendRequests div .friendAccept {
    font-size: 1.6rem;
    position: absolute;
    width: 15rem;
    top: 18rem;
    left: 8.5rem;
    background-color: rgb(48, 138, 48);
    width: 6rem;
    border-radius: 1.5rem;
    box-shadow: 0 1rem 1rem rgba(0, 0, 0, .15);
    cursor: pointer;
    transition: all .2s;
}

.friendRequests div .friendReject {
    font-size: 1.6rem;
    position: absolute;
    width: 15rem;
    top: 18rem;
    left: 0rem;
    background-color: rgb(199, 56, 56);
    width: 6rem;
    border-radius: 1.5rem;
    box-shadow: 0 1rem 1rem rgba(0, 0, 0, .15);
    cursor: pointer;
    transition: all .2s;
}

.friendRequests div .friendAccept:hover,
.friendRequests div .friendReject:hover {
    box-shadow: 0 .5rem .5rem rgba(0, 0, 0, .2);
    transform: translate(-1px, -1px);
}

.friendRequests {
    margin-top: 2rem;
}

